.skills-info-card {
	min-height: 23rem;
	border-radius: 0.65rem;
	border: 1.5px solid #6751b9;
	background-color: rgba(22, 17, 47, 0.398);
	-webkit-border-radius: 0.65rem;
	-moz-border-radius: 0.65rem;
	-ms-border-radius: 0.65rem;
	-o-border-radius: 0.65rem;
}

.skills-info-card h6 {
	font: 1.2rem;
	font-weight: 500;
	padding: 0.8rem 2rem;
	background: linear-gradient(180deg, #ac96ff 0%, #7c5fe6 100%);
	background-clip: text;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	border-bottom: 1.5px solid #6751b9;
}

.skills-info-content {
	padding: 2rem;
}

.skill-info {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.skill-info p {
	font: 1rem;
	font-weight: 500;
}

.skill-info .percentage {
	color: #dd8cfa;
}

.skill-progress-bg {
	width: 100%;
	height: 0.5rem;
	background-color: 382e68;
	border-radius: 0.5rem;
	margin: 1rem 0;
	overflow: hidden;
	-webkit-border-radius: 0.5rem;
	-moz-border-radius: 0.5rem;
	-ms-border-radius: 0.5rem;
	-o-border-radius: 0.5rem;
}
.skill-progress {
	width: 0%;
	height: 0.5rem;
	background: linear-gradient(90deg, #aa94fe -50%, #7d60e6 50%);
	border-radius: 0.5rem;
	transition: all 0.5s ease-in-out;
	-webkit-border-radius: 0.5rem;
	-moz-border-radius: 0.5rem;
	-ms-border-radius: 0.5rem;
	-o-border-radius: 0.5rem;
	-webkit-transition: all 0.5s ease-in-out;
	-moz-transition: all 0.5s ease-in-out;
	-ms-transition: all 0.5s ease-in-out;
	-o-transition: all 0.5s ease-in-out;
}
