.skills-card {
	border-radius: 0.65rem;
	border: 1.5px solid #6751b9;
	padding: 2.5rem;
	text-align: center;
	background-color: rgba(22, 17, 47, 0.398);
	position: relative;
	cursor: pointer;
	transition: all 0.3s ease;
	-webkit-border-radius: 0.65rem;
	-moz-border-radius: 0.65rem;
	-ms-border-radius: 0.65rem;
	-o-border-radius: 0.65rem;
	-webkit-transition: all 0.3s ease;
	-moz-transition: all 0.3s ease;
	-ms-transition: all 0.3s ease;
	-o-transition: all 0.3s ease;
}

.skills-card:hover,
.skills-card.active {
	background: linear-gradient(180deg, #aa94fe 0%, #7c5fe6 100%);
}

.skills-card span {
	font-size: 1.3rem;
	font-weight: 500;
}

.skill-icon {
	width: 4rem;
	height: 4rem;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: #2a2252;
	border-radius: 0.65rem;
	border: 1.5px solid #6852ba;
	position: absolute;
	top: -1rem;
	left: -1rem;
	-webkit-border-radius: 0.65rem;
	-moz-border-radius: 0.65rem;
	-ms-border-radius: 0.65rem;
	-o-border-radius: 0.65rem;
}

.skill-icon img {
	width: 2.4rem;
	height: auto;
	object-fit: contain;
}

@media (max-width: 1025pc) {
	.skills-card {
		padding: 2rem;
	}

	.skills-card span {
		font: 1rem;
	}

	.skill-icon {
		width: 3rem;
		height: 3rem;
	}

	.skill-icon img {
		width: 1.8rem;
	}
}

@media (max-width: 768px) {
	.skills-card span {
		font-size: 0.9rem;
	}
}

